import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import styles from './styles.css'
import inputStyles from '_atoms/input/styles.css'

const LEVELS = [
  {
    value: 1,
    label: 'Novice',
    needleStyle: styles.needleNovice,
    levelStyle: styles.levelNovice,
  },
  {
    value: 2,
    label: 'Beginner',
    needleStyle: styles.needleBeginner,
    levelStyle: styles.levelBeginner,
  },
  {
    value: 3,
    label: 'Skillful',
    needleStyle: styles.needleSkillful,
    levelStyle: styles.levelSkillful,
  },
  {
    value: 4,
    label: 'Experienced',
    needleStyle: styles.needleExperienced,
    levelStyle: styles.levelExperienced,
  },
  {
    value: 5,
    label: 'Expert',
    needleStyle: styles.needleExpert,
    levelStyle: styles.levelExpert,
  },
]

const SkillLevelField = ({
  id,
  name,
  label,
  value,
  onChange
}) => {
  const getCurrentLevel = LEVELS[value -1]

  const moveNeedle = level => ({
    transform: `translateX(${(level - 1) * 100}%)`
  })

  return (
    <div className={inputStyles.container}>
      {label && <label className={inputStyles.label} htmlFor={id}>{label}</label>}
  
      <div className={classNames(inputStyles.input, styles.field, getCurrentLevel.fieldStyle)}>
        <div className={classNames(styles.needle, LEVELS[value - 1].needleStyle)} style={moveNeedle(value)} />
        {LEVELS.map((level, index) => (
          <button 
            key={index}
            type="button" 
            onClick={() => onChange(level.value)} 
            className={styles.level}
          />
        ))}
      </div>
    </div>
  )
}

SkillLevelField.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string,
  label: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
}

SkillLevelField.propTypes = {
  id: '',
  name: '',
  label: '',
  value: '',
  onChange: () => {},
}

export default SkillLevelField
